@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{margin:0;padding:0;list-style:none;font-family:'Montserrat', sans-serif}.tesla-header{display:flex;align-items:center;justify-content:space-between;padding:20px;position:fixed;top:0;left:0;right:0;z-index:10000}@media (max-width: 56.25em){.tesla-header__logo{align-self:flex-start}}.tesla-header__logo img{object-fit:contain;height:20px}.tesla-header__models-list{display:flex}@media (max-width: 56.25em){.tesla-header__models-list{flex-direction:column}}.tesla-header__models-list li{margin:0 10px;text-transform:uppercase;color:#000;font-weight:600;font-size:.8em}.section{width:100%;height:100vh;display:flex;flex-direction:column;justify-content:space-between}.section__info{margin-top:150px}.section__info h1{font-size:2.5rem;color:#393C41}@media (max-width: 37.5em){.section__info h1{font-size:1.9rem}}.section__info label{margin-top:2rem;font-size:0.9rem;color:#393C41}@media (max-width: 37.5em){.section__info label{font-size:0.9rem}}.section__buttons{margin-bottom:50px;display:flex;justify-content:center}@media (max-width: 37.5em){.section__buttons{flex-direction:column}}.section__buttons button{margin:0 20px;padding:15px 70px;border:none;border-radius:20px;cursor:pointer;text-transform:uppercase;font-weight:600;font-size:.8rem}@media (max-width: 37.5em){.section__buttons button{margin-top:20px}}.section__buttons--dark{background-color:rgba(0,0,0,0.8);color:#fff}.section__buttons--light{background-color:rgba(255,255,255,0.8);color:#000}

